/* All components */
@import 'https://use.typekit.net/aoo6xbu.css';
.btn {
  font-size: 0.875em;
  font-weight: 700;
  background-color: #FF951C;
  line-height: 1.07;
  font-family: 'sofia-pro', sans-serif;
  padding-top: 0.75rem;
  padding-bottom: 0.75rem;
  padding-left: 3rem;
  padding-right: 3rem;
  color: #ffffff;
  appearance: none;
  border-width: 1px;
  border-color: transparent;
  border-radius: 5px;
  cursor: pointer;
}
.btn:hover:not(.disabled) {
  background-color: #FE7B02;
}
.btn.disabled {
  background-color: #FFCC9D;
  pointer-events: none;
}
.btn.secondary {
  border-width: 1px;
  border-color: #FF951C;
  background-color: transparent;
  color: #FF951C;
}
.btn.secondary:hover:not(.disabled) {
  background-color: transparent;
  border-color: #FE7B02;
  color: #FE7B02;
}
.btn.secondary.disabled {
  opacity: 0.6;
}
.main-nav .navlink svg * {
  transition: all 0.25s ease;
}
.main-nav li .navlink.active svg * {
  fill: #FF951C;
}
.common-input::placeholder {
  font-size: 0.875em;
  color: #dddbe6;
  font-weight: 400;
  font-family: 'sofia-pro', sans-serif;
}
.toggle {
  position: relative;
  width: 2.5rem;
  height: 1.5rem;
}
.toggle input {
  opacity: 0;
  width: 0rem;
  height: 0rem;
}
.slider {
  position: absolute;
  cursor: pointer;
  bottom: 0;
  top: 0;
  left: 0;
  right: 0;
  border-radius: 35px;
  border-style: solid;
  border-width: 1px;
  border-color: #8782a6;
  background-color: #ffffff;
}
.slider:before {
  position: absolute;
  content: "";
  width: 1.25rem;
  height: 1.25rem;
  left: 2px;
  bottom: 1px;
  background-color: #8782a6;
  border-radius: 35px;
  transition: all 0.25s ease;
}
input:checked + .slider {
  border-color: #FF951C;
}
input:checked + .slider:before {
  transform: translate3d(14px, 0, 0);
  background-color: #FF951C;
}
.radio-button {
  position: relative;
  cursor: pointer;
}
.radio-button input {
  appearance: none;
  outline: none;
  border-radius: 50px;
  width: 1.75rem;
  height: 1.75rem;
  border-color: #e6e6e6;
  border-style: solid;
  border-width: 1px;
  background-color: #ffffff;
  transition: all 0.25s ease;
  cursor: pointer;
}
.radio-button input:checked {
  border-color: #FF951C;
  border-style: solid;
  border-width: 1px;
}
.radio-button input:checked + .radio-background {
  display: block;
}
.radio-button .radio-background {
  display: none;
  position: absolute;
  bottom: 0;
  top: 0;
  width: 1.25rem;
  height: 1.25rem;
  top: 50%;
  left: 0;
  transform: translate3d(4px, -50%, 0);
  background-color: #FF951C;
  border-radius: 50px;
}
.route-wrapper {
  animation: route-enter 0.25s cubic-bezier(0.18, -0.38, 0.28, 1.34) forwards;
}
.grid-main {
  animation: main-enter 0.25s ease forwards;
}
@keyframes route-enter {
  0% {
    opacity: 0;
    transform: translate3d(-20px, 0, 0);
  }
  100% {
    opacity: 1;
    transform: translate3d(0, 0, 0);
  }
}
@keyframes main-enter {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
main.is-fullscreen {
  position: absolute;
  z-index: 2147483638;
  background-color: #ffffff;
  padding: 0rem;
  width: 100%;
  min-height: 100vh;
}
main.is-fullscreen .full-width-height-component {
  position: absolute;
  top: 0;
  left: 0;
  min-width: 100vw;
}
.modal-wrapper {
  position: absolute;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  z-index: 2147483638;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-items: center;
  justify-content: center;
}
.modal-wrapper .modal-overlay {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: 1;
}
.modal-wrapper .modal-content {
  position: relative;
  background-color: #fafafa;
  z-index: 20;
  border-radius: 8px;
  overflow: hidden;
  box-shadow: 0 2px 8px 0 rgba(235, 230, 230, 0.56);
  animation: modal-appear 0.2s ease-out forwards;
}
.modal-wrapper .modal-content .modal-header {
  padding-left: 2.5rem;
  padding-right: 2.5rem;
  padding-top: 1.75rem;
  padding-bottom: 1.75rem;
  text-align: left;
  background-color: #ffffff;
}
.modal-wrapper .modal-content .modal-body {
  padding-left: 2.5rem;
  padding-right: 2.5rem;
  padding-top: 2.5rem;
  padding-bottom: 2.5rem;
}
@media screen and (max-width: 576px) {
  .modal-wrapper .modal-content {
    width: 100vw;
    height: 100vh;
    border-radius: 0px;
  }
  .modal-wrapper .modal-content .modal-header,
  .modal-wrapper .modal-content .modal-body {
    padding-left: 1.25rem;
    padding-right: 1.25rem;
    padding-top: 1.5rem;
    padding-bottom: 1.5rem;
  }
  .modal-wrapper .modal-content .modal-body {
    padding-top: 1rem;
    padding-bottom: 1rem;
  }
}
.modal-arrow {
  position: absolute;
  right: 1.25rem;
  top: 1.75rem;
  width: 1rem;
  height: 1rem;
  cursor: pointer;
  transition: all 0.25s ease;
}
.modal-arrow:before,
.modal-arrow:after {
  content: '';
  display: block;
  width: 100%;
  height: 2px;
  background-color: #493E69;
  border-radius: 10px;
  transform: translate3d(3px, 9px, 0) rotate(45deg);
}
.modal-arrow:after {
  transform: translate3d(3px, 7px, 0) rotate(-45deg);
}
.modal-arrow:hover {
  transform: scale(0.8);
  opacity: 0.8;
}
@keyframes modal-appear {
  0% {
    opacity: 0.5;
    transform: scale(0.9);
  }
  100% {
    opacity: 1;
    transform: scale(1);
  }
}
.overlay {
  position: relative;
}
.overlay::before {
  content: "";
  position: absolute;
  top: 0rem;
  left: 0rem;
  width: 100%;
  height: 100%;
  z-index: 40;
  background-color: rgba(0, 0, 0, 0.5);
}
.overlay-item {
  display: none;
}
.overlay-item.is-visible {
  display: block;
  animation: overlay-item-appear 0.2s ease forwards;
}
@keyframes overlay-item-appear {
  0% {
    opacity: 0.8;
    transform: translate3d(0, 15px, 0);
  }
  100% {
    opacity: 1;
    transform: translate3d(0, 0, 0);
  }
}
.translate-X-100 {
  transform: translateX(-100%);
}
.transition-side-menu {
  transition: transform 0.3s ease-out;
}
.transform-0 {
  transform: translateX(0);
}
.bg-backdrop {
  background: rgba(0, 0, 0, 0.3);
}
